<template>
	<form @submit='onClick'>
		<input v-model= 'minPrice' placeholder='minPrice'>
		<input v-model='maxPrice' placeholder='maxPrice'>
		<button >Search</button>
	</form>
</template>

<script>
	export default {
		name: 'AddressSearch',
		data () {
			return {
				minPrice: '',
				maxPrice: '',

			}
		},
		props: ['handleClick', 'bounds'],
		methods: {
			onClick() {
				console.log('click');
				if(typeof(this.minPrice === 'number' && typeof(this.maxPrice === 'number'))){
					this.handleClick({
						minPrice: this.minPrice,
						maxPrice: this.maxPrice,
						bounds: this.bounds
					});
				}
			}
		}
	}
</script>

<style scoped>
input{
	padding: 5px;
}
</style>


